.navElement {
    opacity: 0.25;
    transition: opacity 0.3s ease; /*  */
  }
  
  .navElement:hover {
    opacity: 1;
   
  }
  .navElement .BtnUp {
    transition: transform 0.3s ease; /* 0.3 ease  */
  }
  
  .navElement:hover .BtnUp {
    transform: translateY(0.15rem) rotate(15deg) translateZ(0px);
  }
  
  
  .navElement .BtnDown {
    transition: transform 0.3s ease; /* 0.3 ease  */
  }
  
  .navElement:hover .BtnDown {
    transform: translateY(-0.15rem) rotate(-15deg) translateZ(0px);
  }
  
  .BtnDown{
    transform: translateY(-0.15rem) rotate(0deg) translateZ(0px);
  
  }
  
  
  /*  */
  /* Default style for screen sizes greater than 765px */
  .model-component {
    transform: translate3d(268px, 50px, 0px);
  }
  
  /* Media query for screen sizes less than or equal to 765px */
  @media (max-width: 765px) {
    .model-component {
      transform: translate3d(10px, 100px, 0px);
    }
  }
  
  
  
  .result-title-streaming>:not(ol):not(ul):not(pre):last-child:after,
  .result-title-streaming>ol:last-child li:last-child:after,
  .result-title-streaming>pre:last-child code:after,
  .result-title-streaming>ul:last-child li:last-child:after {
      -webkit-animation: blink 1s steps(5, start) infinite;
      animation: blink 1s steps(5, start) infinite;
      animation-play-state: running;
      content: "";
      margin-left: .25rem;
      vertical-align: baseline;
  
      
  
  }
  
  .result-title-streaming > .bg-black:not(ol):not(ul):not(pre):last-child:after,
  .result-title-streaming > .bg-black ol:last-child li:last-child:after,
  .result-title-streaming > .bg-black pre:last-child code:after,
  .result-title-streaming > .bg-black ul:last-child li:last-child:after {
      -webkit-animation: blink 1s steps(5, start) infinite;
      animation: blink 1s steps(5, start) infinite;
      animation-play-state: running;
      content: "";
      margin-left: .25rem;
      vertical-align: baseline;
  
    color: white;
  }
  
  